const initState = {
    themeId: 1,
}

export type changeThemeIdAT = ReturnType<typeof changeThemeId>

export const themeReducer = (state = initState, action: changeThemeIdAT): any => { // fix any
    switch (action.type) {
        case 'SET_THEME_ID': {
            return {
                ...state,
                themeId: action.id
            }
        }

        default:
            return state
    }
}


export const changeThemeId = (id: number): any => ({type: 'SET_THEME_ID', id} as const) // fix any

